  margin: 0;
}

a,
a:active,
a:hover,
a:focus,
a:visited {
  text-decoration: none;
  cursor: pointer;
  transition: all 0.15s ease-in-out;
}

input,
button {
  outline: none;
  transition: all 0.15s ease-in-out;
}

.input-control {
  /* Input form Style */
  /* Label style after Input feild is in focus. Can also use input:focus ~ label to select sibling. */
}
.input-control input {
  border: 1px solid #D6D6D6;
  border-radius: 5px;
  height: 60px;
  box-shadow: none;
  padding: 0px 20px;
  font-weight: 500;
}
.input-control input:focus,
.input-control input:valid {
  padding-top: 10px;
}
.input-control input:focus + label,
.input-control input:valid + label {
  font-size: 12px;
  color: #777777;
  top: 5px;
  left: 20px;
}
.input-control .input_wrap {
  width: auto;
  height: auto;
  position: relative;
}
.input-control .input_wrap label {
  color: #CCCCCC;
  position: absolute;
  top: 18px;
  left: 20px;
  -moz-transition: 0.2s ease all;
  -webkit-transition: 0.2s ease all;
  transition: 0.2s ease all;
  pointer-events: none;
}
.input-control input:focus {
  outline: none;
}

.checkbox_wrap {
  gap: 16px;
}
.checkbox_wrap .checkbox-button {
  cursor: pointer;
  display: inline-flex;
  align-items: center;
}
.checkbox_wrap input[type=checkbox],
.checkbox_wrap input[type=radio] {
  box-sizing: border-box;
  padding: 0;
}
.checkbox_wrap input {
  font-size: 1rem;
  line-height: 1.5;
  padding: 11px 23px;
  border: 1px solid #D6D6D6;
  border-radius: 0;
  outline: 0;
  background-color: transparent;
}
.checkbox_wrap .checkbox-button__input {
  opacity: 0;
  position: absolute;
}
.checkbox_wrap .checkbox-button__control {
  position: relative;
  display: inline-flex;
  width: 24px;
  height: 24px;
  margin-right: 15px;
  vertical-align: middle;
  background-color: inherit;
  color: #D0D0D0;
  border: 1px solid #D0D0D0;
  border-radius: 2px;
}
.checkbox_wrap .checkbox-button__input:checked + .checkbox-button__control:after {
  content: "";
  display: block;
  position: absolute;
  top: 4px;
  left: 4px;
  width: 14px;
  height: 14px;
  background-color: #f6931e;
  border-radius: 2px;
}
.checkbox_wrap .checkbox-button__input:checked + .checkbox-button__control {
  border-color: #f6931e;
}

